:host {
  display: block;
  padding: 30px 40px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f0f4f8;
  min-height: 100vh;
  box-sizing: border-box;
}

/* ============================
    TÍTULO PRINCIPAL
============================ */
h2 {
  font-size: 32px;
  color: #164d6c;
  margin-bottom: 35px;
  border-left: 8px solid #2c8ead;
  padding-left: 16px;
  font-weight: 700;
}

/* ============================
    GRID PRINCIPAL
============================ */
.carrito-grid {
  display: grid;
  grid-template-columns: 2.5fr 1fr;
  gap: 50px;
  align-items: start;
}

/* ============================
    LISTA DE PRODUCTOS
============================ */
.carrito-productos {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 44px;
}

/* ============================
    TARJETA DE PRODUCTO
============================ */
.producto-carrito {
  background-color: #ffffff;
  border-radius: 12px;
  box-shadow: 0 4px 14px rgba(0, 0, 0, 0.08);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  transition: transform 0.25s ease, box-shadow 0.25s ease;
}

.producto-carrito:hover {
  transform: translateY(-6px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.18);
}

/* Imagen */
.producto-carrito img {
  width: 100%;
  height: 300px;
  object-fit: cover;
  border-bottom: 3px solid #2c8ead;
}

/* Info */
.producto-info {
  padding: 20px 25px;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.producto-info h4 {
  margin: 0;
  font-size: 22px;
  font-weight: 700;
  color: #164d6c;
}

.producto-info p {
  margin: 0;
  font-size: 15px;
  color: #555f68;
  line-height: 1.5;
}

.producto-info p strong {
  color: #2c8ead;
  font-weight: 700;
}

/* ============================
    CONTROL DE CANTIDAD
============================ */
.cantidad-control {
  display: flex;
  flex-direction: column;
  gap: 6px;
  margin-top: 10px;
}

.cantidad-control label {
  font-weight: 700;
  color: #3c5168;
}

.cantidad-control div {
  display: flex;
  align-items: center;
  gap: 12px;
}

.cantidad-control button {
  background-color: #2c8ead;
  border: none;
  color: white;
  font-size: 18px;
  padding: 6px 12px;
  border-radius: 6px;
  cursor: pointer;
  transition: 0.3s ease;
}

.cantidad-control button:hover {
  background-color: #1a607b;
}

.cantidad-control input {
  width: 65px;
  padding: 6px 0;
  font-size: 15px;
  text-align: center;
  border: 1.5px solid #bbb;
  border-radius: 6px;
  font-weight: 600;
  color: #2c3e50;
  transition: border-color 0.3s ease;
}

.cantidad-control input:focus {
  border-color: #2c8ead;
}

/* ============================
    BOTÓN ELIMINAR
============================ */
.btn-eliminar {
  margin-top: 15px;
  background-color: #e94e4e;
  border: none;
  color: white;
  font-weight: 700;
  padding: 10px 18px;
  border-radius: 8px;
  cursor: pointer;
  transition: 0.3s ease;
  width: fit-content;
}

.btn-eliminar:hover {
  background-color: #b02a2a;
}

/* ============================
    RESUMEN DEL CARRITO
============================ */
.carrito-resumen {
  background-color: #ffffff;
  padding: 28px 30px;
  border-radius: 16px;
  box-shadow: 0 6px 18px rgba(44, 142, 173, 0.25);
  position: sticky;
  top: 30px;
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 22px;
}

.carrito-resumen h3 {
  margin: 0;
  font-size: 26px;
  font-weight: 800;
  color: #164d6c;
}

.carrito-resumen p {
  font-size: 18px;
  color: #2c8ead;
}

.carrito-resumen button {
  background-color: #2c8ead;
  color: white;
  font-weight: 700;
  font-size: 18px;
  border: none;
  border-radius: 12px;
  padding: 14px 0;
  cursor: pointer;
  transition: 0.3s ease;
}

.carrito-resumen button:hover {
  background-color: #1a607b;
}

/* ============================
    RESPONSIVE
============================ */

/* Tablets */
@media (max-width: 1024px) {
  .carrito-grid {
    grid-template-columns: 1fr;
    gap: 40px;
  }

  .carrito-resumen {
    position: static;
  }

  .producto-carrito img {
    height: 240px;
  }
}

/* Móviles grandes */
@media (max-width: 768px) {
  h2 {
    font-size: 26px;
  }

  .carrito-productos {
    grid-template-columns: 1fr;
  }

  .producto-carrito img {
    height: 220px;
  }
}

/* Móviles pequeños */
@media (max-width: 480px) {
  :host {
    padding: 20px;
  }

  h2 {
    font-size: 24px;
  }

  .producto-carrito img {
    height: 180px;
  }

  .carrito-resumen {
    padding: 20px;
  }
}
